
'use client';

import { useState } from 'react';
import { useAuth } from '@/hooks/use-auth';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent } from '@/components/ui/card';
import { Avatar, AvatarImage, AvatarFallback } from '@/components/ui/avatar';
import { Paperclip, Send, X } from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';
import { useToast } from '@/hooks/use-toast';
import { format } from 'date-fns';

// Mock data for comments - replace with Firestore fetching
const MOCK_COMMENTS = [
    { id: 'c1', author: 'Alex Ray', authorAvatar: 'https://i.pravatar.cc/150?u=alex', content: 'Great article! The mountain views look absolutely stunning.', image: null, createdAt: new Date() },
    { id: 'c2', author: 'Sara Connor', authorAvatar: 'https://i.pravatar.cc/150?u=sara', content: 'I did a similar trip last year. Here is a photo from my hike!', image: 'https://picsum.photos/seed/comment-hike/400/200', createdAt: new Date(Date.now() - 1000 * 60 * 5) }
];


export function CommentSection({ postId }: { postId: string }) {
    const { user, loading } = useAuth();
    const { toast } = useToast();
    const [comment, setComment] = useState('');
    const [image, setImage] = useState<File | null>(null);
    const [imagePreview, setImagePreview] = useState<string | null>(null);

    const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            setImage(file);
            setImagePreview(URL.createObjectURL(file));
        }
    };

    const handleRemoveImage = () => {
        setImage(null);
        setImagePreview(null);
    }

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (!comment.trim()) {
            toast({
                title: 'Comment cannot be empty.',
                variant: 'destructive',
            });
            return;
        }
        // Firestore logic to save comment would go here
        console.log({
            postId,
            userId: user?.uid,
            comment,
            image,
        });
        toast({
            title: 'Success!',
            description: 'Your comment has been submitted.',
        });
        setComment('');
        setImage(null);
        setImagePreview(null);
    };

    if (loading) {
        return <p>Loading comments...</p>;
    }
    
    return (
        <div>
            <h2 className="text-3xl font-bold font-headline mb-8">Join the Conversation</h2>
            {user ? (
                <Card className="mb-8 shadow-lg rounded-2xl border-none">
                    <CardContent className="p-6">
                        <form onSubmit={handleSubmit} className="flex items-start gap-4">
                             <Avatar>
                                <AvatarImage src={user.photoURL || undefined} alt={user.displayName || 'User'} />
                                <AvatarFallback>{user.displayName?.charAt(0) || user.email?.charAt(0)}</AvatarFallback>
                            </Avatar>
                            <div className="w-full">
                                <Textarea
                                    value={comment}
                                    onChange={(e) => setComment(e.target.value)}
                                    placeholder="Add a comment or share your follow-up story..."
                                    className="mb-2 rounded-xl"
                                    rows={3}
                                />
                                {imagePreview && (
                                    <div className="relative w-48 h-24 mb-2">
                                        <Image src={imagePreview} alt="Image preview" fill className="rounded-md object-cover" />
                                        <Button
                                            type="button"
                                            variant="destructive"
                                            size="icon"
                                            className="absolute -top-2 -right-2 h-6 w-6 rounded-full"
                                            onClick={handleRemoveImage}
                                        >
                                            <X className="h-4 w-4" />
                                        </Button>
                                    </div>
                                )}
                                <div className="flex justify-between items-center">
                                    <Button asChild variant="ghost" size="icon" className="cursor-pointer">
                                        <label htmlFor="image-upload">
                                            <Paperclip className="h-5 w-5" />
                                            <input id="image-upload" type="file" accept="image/*" className="hidden" onChange={handleImageChange} />
                                        </label>
                                    </Button>
                                    <Button type="submit">
                                        <Send className="mr-2 h-4 w-4" />
                                        Post
                                    </Button>
                                </div>
                            </div>
                        </form>
                    </CardContent>
                </Card>
            ) : (
                <div className="text-center bg-secondary p-8 rounded-2xl">
                    <p className="mb-4">You need to be logged in to join the conversation.</p>
                    <Button asChild>
                        <Link href="/login">Login to Comment</Link>
                    </Button>
                </div>
            )}

            <div className="space-y-6">
                {MOCK_COMMENTS.map(c => (
                     <div key={c.id} className="flex items-start gap-4">
                        <Avatar>
                            <AvatarImage src={c.authorAvatar} alt={c.author} />
                            <AvatarFallback>{c.author.charAt(0)}</AvatarFallback>
                        </Avatar>
                        <div className="w-full">
                            <div className="bg-secondary p-4 rounded-xl rounded-tl-none">
                                <div className="flex items-center justify-between mb-1">
                                    <p className="font-bold">{c.author}</p>
                                    <p className="text-xs text-muted-foreground">{format(c.createdAt, 'MMM d, yyyy')}</p>
                                </div>
                                <p className="text-foreground/90">{c.content}</p>
                                {c.image && (
                                    <div className="mt-4 aspect-video relative rounded-lg overflow-hidden">
                                        <Image src={c.image} alt="Comment image" fill className="object-cover" />
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
